* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  background: linear-gradient(to right, #ce5937 0%, #1c6ea4 50%, #c59237 100%);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

body > p > a {
  width: 150px;
  text-align: center;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 7px;
  padding: 5px;
  text-decoration: none;
  display: block;
}

img {
  max-width: 100%;
  display: block;
}

.main-menu {
  margin-top: 50px;
  list-style: none;
}

.main-menu > li {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.main-menu > li > a {
  width: 350px;
  color: #fff;
  text-shadow: 0 10px 20px rgba(0, 0, 0, .19), 0 6px 6px rgba(0, 0, 0, .23);
  text-align: center;
  background-color: #8de2df;
  border: 2px solid gray;
  border-radius: 10px;
  padding: 5px;
  text-decoration: none;
  display: block;
}

.main-menu > li {
  z-index: 0;
  width: 350px;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin: 30px auto;
  padding: 5px;
  font-family: Roboto, sans-serif;
  font-size: 24px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.main-menu > li:before {
  content: "";
  z-index: -2;
  width: 200%;
  height: 200%;
  background-color: #bfe2ff;
  background-image: linear-gradient(#ffbdbd, #79e018), linear-gradient(#8bb733, #b73389), linear-gradient(#42ecc2, #cabfff), linear-gradient(#b73389, #acb733);
  background-position: 0 0, 100% 0, 100% 100%, 0 100%;
  background-repeat: no-repeat;
  background-size: 50% 50%, 50% 50%;
  animation: anim-ramka-5 3.5s linear infinite;
  position: absolute;
  top: -50%;
  left: -50%;
}

.main-menu > li:after {
  content: "";
  z-index: -1;
  width: calc(100% - 12px);
  height: calc(100% - 12px);
  background: #fff;
  border-radius: 5px;
  position: absolute;
  top: 6px;
  left: 6px;
}

@keyframes anim-ramka-5 {
  100% {
    transform: rotate(1turn);
  }
}

